                    self.errorMessage = "Anda Gagal !";
                    $("#btnNext").attr("disabled", false);
                    $("#btnHapus").attr("disabled", false);
                }
            })
            .catch(function(error) {
                console.log(error);
            });


            }else if (self.imagetemp !== null && self.jawabanUser[self.indexSoal] !== undefined){ 

